
#app {
  font-family: "Ubuntu", sans-serif;
}

.welcome {
  max-width: 400px;
  margin-top: 100px;
}
.welcome h2 {
  font-size: 3em;
}
.welcome button {
  margin: 34px auto;
}

.chat h2 {
  font-size: 2.1em;
  margin-bottom: 55px;
}
.chat span {
  font-size: 1.3em;
}
.chat .time {
  display: block;
  font-size: 0.8em;
}
.messages {
  max-height: 300px;
  overflow: auto;
}
.messages::-webkit-scrollbar {
  width: 3px;
}
.messages::-webkit-scrollbar-track {
  background: #dddddd;
}
.messages::-webkit-scrollbar-thumb {
  background: #aaaaaa;
}













































